{
  height: 100vh;
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  /* background-image: url('./Bg/bg_2.jpg');
  background-size: cover;
  background-position: center; */
}
.left-container
{
  -webkit-flex: 0.6 1;
          flex: 0.6 1;
