            align-items: center;
}
.icon-container
{
    height: 100px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}
.MainInfo-body
{
    height: 100vh;
    width: 100%;
    font-family: 'Poppins', sans-serif;
    min-width: 400px;
    /* border: 2px solid black; */
    color: white;
    /* background-color: rgb(0,0,0); */
    background-color: rgba(0,0,0, 0.4); 
    -webkit-backdrop-filter: blur(1px);
    backdrop-filter: blur(5px);
}
/* --------- Input Section --------*/
.input {
    height: 10%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    -webkit-align-items: center;
            align-items: center;

}
.input-container
{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    -webkit-align-items: center;
            align-items: center;
    width: 100%;
    height: 100%;
}
.input-container input
{
    width: 80%;
    margin-left: 50px;
    background: transparent;
    outline: none;
    border: none;
    border-bottom: 2px solid #9c9c9c;
    font-size: 20px;
    color: white;
    padding: 20px;
    
}
.input-container input::-webkit-input-placeholder
{
    color: #dedede;
}
.input-container input:-ms-input-placeholder
{
    color: #dedede;
}
.input-container input::placeholder
{
    color: #dedede;
}
.input input:focus
{
    border-bottom: 2px solid white;
    transition: border-bottom 0.3s;
}
.search-button
{
    height: 100%;
    width: 15%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    min-width: 80px;
    border: 1px solid white;
    background-color: #e6e7e8;
    opacity: 0.4;
}
.search-button:active
{
    background-color: white;
    transition: background-color 0.3s;
}

.search-button img
{
    height: 40px;
    width: 40px;
}
/* --------- Locations Section --------*/
.locations
{
    height: 45%;
}
.location-container
{
    height: 100%;
    margin-left: 30px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-around;
            justify-content: space-around;
}
.compacter
{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
}
.compacter > button 
{
    margin-left: 35px;
    border: none;
    outline: none;
    padding: 10px 8px;
    border-radius: 4px;
    font-family: 'Poppins', sans-serif;
    background-color: #e6e7e8;
    opacity: 0.4;
    border: 2px solid transparent;
}
.compacter > button:active
{
    background-color: white;
    transition: background-color 0.2s;
    border: 2px solid white;
}
.location-container > h3
{
    margin-top: 10px;
}


/* --------- Info Section --------*/

.details
{
    height: 45%;
}
.details-container
{
    height: 100%;
    margin-left: 30px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-around;
            justify-content: space-around;
}
.details-container > h3
{
    margin-top: 10px;
}

.city-components
{
    border-bottom: 2px solid transparent;
}
.city-components:hover
{
    border-bottom: 2px solid white;
    transition-property: border;
    transition-duration: 1s;
}
.city-components:active
{
    font-weight: 600;
}
